<template>
  <div class="cashback-history-page">
    <div class="header-cashback">
      <div class="title">
        <h1>Cashback History</h1>
        <h3>ATA Rent Recovery Transactions</h3>
      </div>
    </div>
    
    <div class="main-content">
      <div class="history-container">
        <div class="filter-bar">
          <div class="back-link">
            <router-link to="/cashback" class="back-button">
              <i class="fas fa-arrow-left"></i> Back to Cashback
            </router-link>
          </div>
          
          <div class="stats-container">
            <div class="stat-card">
              <div class="stat-icon">
                <i class="fas fa-coins"></i>
              </div>
              <div class="stat-info">
                <div class="stat-value">{{ stats.solAmount }} SOL</div>
                <div class="stat-label">Total Reclaimed</div>
              </div>
            </div>
            
            <div class="stat-card">
              <div class="stat-icon">
                <i class="fas fa-database"></i>
              </div>
              <div class="stat-info">
                <div class="stat-value">{{ stats.ataAmount }}</div>
                <div class="stat-label">ATAs Processed</div>
              </div>
            </div>
          </div>
        </div>
        
        <div v-if="loading" class="loading">
          <i class="fas fa-spinner fa-spin"></i> Loading transaction history...
        </div>
        
        <div v-else-if="error" class="error">
          <i class="fas fa-exclamation-circle"></i> {{ error }}
        </div>
        
        <div v-else-if="history.length === 0" class="no-data">
          <p>No transactions found</p>
        </div>
        
