                <div class="wallet">
                  <i class="fas fa-wallet"></i>
                  <div class="wallet-text">
                    <div class="wallet-address">{{ formatWalletAddress(item.walletAddress) }}</div>
                    <div class="wallet-copy" @click="copyToClipboard(item.walletAddress)">
                      <i class="fas fa-copy"></i>
                    </div>
                  </div>
                </div>
                <div class="amount">
                  <i class="fas fa-coins"></i> +{{ item.solAmount }} SOL
                </div>
                <div class="accounts">
                  <i class="fas fa-database"></i> {{ item.accountsCount }} ATAs
                </div>
                <div class="type" :class="{'type-burn': item.isBurn, 'type-reclaim': !item.isBurn}">
                  <i :class="item.isBurn ? 'fas fa-fire' : 'fas fa-recycle'"></i>
                  {{ item.isBurn ? 'Burn' : 'Reclaim' }}
                </div>
              </div>
              <div class="history-row details-row">
                <div class="detail-group">
                  <div class="detail-item cashback-id" v-if="item.cashbackId">
                    <i class="fas fa-hashtag"></i> ID: {{ item.cashbackId }}
                  </div>
                  <div class="detail-item user-tag" v-if="item.userTag">
                    <i class="fas fa-user"></i> {{ item.userTag }}
                  </div>
                </div>
                <div class="detail-group">
                  <div class="detail-item date">
                    <i class="far fa-calendar-alt"></i> {{ formatDate(item.date) }}
                  </div>
                </div>
                <div class="detail-group transactions">
                  <a v-if="item.closeTx" :href="`https://solscan.io/tx/${item.closeTx}`" target="_blank" class="tx-link">
                    <i class="fas fa-external-link-alt"></i> Close TX
                  </a>
                  <a v-if="item.sendTx" :href="`https://solscan.io/tx/${item.sendTx}`" target="_blank" class="tx-link">
                    <i class="fas fa-external-link-alt"></i> Send TX
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
        
        <div v-if="pagination.totalPages > 1" class="pagination">
          <button 
            @click="changePage(pagination.page - 1)" 
            :disabled="pagination.page === 1"
            class="page-button"
          >
            <i class="fas fa-chevron-left"></i>
          </button>
          
          <div class="page-info">
            Page {{ pagination.page }} of {{ pagination.totalPages }}
          </div>
          
          <button 
            @click="changePage(pagination.page + 1)" 
            :disabled="pagination.page === pagination.totalPages"
            class="page-button"
          >
            <i class="fas fa-chevron-right"></i>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import axios from 'axios';

export default {
  name: 'CashbackHistoryPage',
  setup() {
    const history = ref([]);
    const loading = ref(true);
    const error = ref(null);
    const pagination = ref({
      page: 1,
      limit: 20,
      total: 0,
      totalPages: 0
    });
    const stats = ref({
      solAmount: '0.00',
      ataAmount: 0
    });
    
    const fetchHistory = async (page = 1) => {
      try {
        loading.value = true;
        error.value = null;
        
        const response = await axios.get('/ata-cashback/history', {
          params: {
            page,
            limit: pagination.value.limit
          }
        });
        
        if (response.data.success) {
          history.value = response.data.history;
          pagination.value = response.data.pagination;
        } else {
          error.value = 'Failed to load transaction history';
        }
      } catch (err) {
        console.error('Error fetching cashback history:', err);
        error.value = 'Error loading transaction history';
      } finally {
        loading.value = false;
      }
    };
    
    const fetchStats = async () => {
      try {
        const response = await axios.get('/ata-cashback/stats');
        if (response.data.success && response.data.stats) {
          stats.value = response.data.stats;
        }
      } catch (error) {
        console.error('Error fetching ATA cashback stats:', error);
      }
    };
    
    const changePage = (newPage) => {
      if (newPage >= 1 && newPage <= pagination.value.totalPages) {
        pagination.value.page = newPage;
        fetchHistory(newPage);
        // Прокрутка вверх страницы при смене страницы
        window.scrollTo(0, 0);
      }
    };
    
    const formatWalletAddress = (address) => {
      if (!address) return '';
      return `${address.substring(0, 4)}...${address.substring(address.length - 4)}`;
    };
    
    const copyToClipboard = (text) => {
      navigator.clipboard.writeText(text)
        .then(() => {

          // alert('Wallet address copied to clipboard!');
        })
        .catch(err => {
          console.error('Failed to copy text: ', err);
        });
    };

    const formatDate = (date) => {
      if (!date) return '';
      const d = new Date(date);
      return d.toLocaleDateString('en-US', { 
        month: 'short', 
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    };
    
    onMounted(() => {
      fetchHistory();
      fetchStats();
    });
    
    return {
      history,
      loading,
      error,
      pagination,
      stats,
      changePage,
      formatWalletAddress,
      copyToClipboard,
      formatDate
    };
  }
};
</script>

